:root {
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background: radial-gradient(circle, #2b2c2e 13%, #191919 65%);
  justify-content: center;
  align-items: center;
  font-family: Comic Neue, cursive;
  display: flex;
}

.card-container {
  width: 100rem;
  display: flex;
}

.card {
  width: 50%;
  height: 80vh;
  color: #fff;
  text-align: center;
  background-position: center;
  background-size: cover;
  border-radius: 2.5rem;
  margin: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: 5px 5px 10px #000;
}

.card-one {
  background-image: url("../../../portrait-lady.37f49ed4.jpg");
}

.card-two {
  background-image: url("../../../portrait-sir.87aeb40f.jpg");
}

button {
  color: #fff;
  text-shadow: 2px 2px 5px #000;
  cursor: crosshair;
  z-index: 1;
  background: none;
  border: none;
  font-size: 3.5rem;
  position: absolute;
  top: 30px;
  right: 30px;
}

h1 {
  color: #b6b361;
  font-size: 4rem;
}

h2 {
  color: #e3e1e1;
  font-size: 2.5rem;
}

p {
  font-size: 1.4rem;
}

.text {
  clip-path: circle(0% at 50% 80%);
  background-color: #000000b3;
  flex-direction: column;
  justify-content: flex-end;
  padding: 8rem 3rem;
  transition: all .8s;
  display: flex;
  position: absolute;
  inset: 0;
}

button:hover ~ .text {
  clip-path: circle(120% at 50% 80%);
}

